import React, { Component } from "react";

class Roster_hover_zabrome extends Component {
  constructor() {
    super();
    this.state = { isHover: false };
  }

  reveal = () => {
    this.setState({ isHover: !this.state.isHover });
  };

  render() {
    const { isHover } = this.state;
    return (
      <div
        className="card mask"
        onMouseEnter={this.reveal}
        onMouseLeave={this.reveal}
      >
        {isHover ? (
          <div className="flex" style={{ height: "100%" }}>
            <div className="flex">
              <div className="above">Gold</div>
            </div>
            <div className="bg-letter name">Zabrome</div>
            <div className="flex">
              <div className="under">League of Legends</div>
              <div className="under">Civ 5</div>
              <div className="under">Binding of Isaac</div>
              <div className="under">Factorio</div>
              <div className="under">Minecraft</div>
              <div className="under">Terraria</div>
            </div>
          </div>
        ) : (
          <div>Zabrome</div>
        )}
      </div>
    );
  }
}

export { Roster_hover_zabrome };
