import React from "react";

class About extends React.Component {
  render() {
    return (
      <div style={{ backgroundColor: "rgb(120,121,188)" }}>
        <div className="h1-class">
          <h1>Memes made dreams</h1>
          <p>
            Hong Kong Pickles is the fastest growing pickles based online
            community! With more than 5 members, we proudly promote a low toxic
            environment and gameplay that focuses on fun!
          </p>

          <p>
            Whether it's league of Legends, Civilization 5, CS:GO, or our very
            own christian minecraft server HKP is proud to host events any real
            gamer would want to be a part of.
          </p>

          <h1>so what are you waiting for?</h1>
        </div>
      </div>
    );
  }
}

export default About;
