import React from "react";
import "./App.css";
import icon from "../../hkp1/src/Pics/HKP-icon.png";
import Roster from "./Components/roster";
import About from "./Components/About";
import Accolades from "./Components/Accolades";
import Contact from "./Components/Contact";

class App extends React.Component {
  constructor() {
    super();
    this.state = {
      isRoster: false,
      isAbout: false,
      isAccolades: false,
      isContact: false,
      opacity: 0.3,
      opacity2: 0.3,
      opacity3: 0.3,
      isHover1: false,
      isHover2: false,
      isHover3: false,
      fontSize: "small",
      fontSize2: "small",
      fontSize3: "small",
      isAboutClicked: false,
      isAccoladesClicked: false,
      isContactClicked: false,
      width: window.innerWidth,
      height: window.innerHeight,
      setWidth: "590px",
      setHeight: "200px"
    };
  }

  componentDidMount() {
    this.update();
    window.addEventListener("resize", this.update);
  }
  componentWillUnmount() {
    window.removeEventListener("resize", this.update);
  }

  update = () => {
    this.setState({ width: window.innerWidth, height: window.innerHeight });
    if (this.state.width >= 730) {
      this.setState({ setWidth: "590px", setHeight: "200px" });
    } else {
      this.setState({ setWidth: "350px", setHeight: "90px" });
    }
  };

  isRoster_function = () => {
    this.setState({
      isRoster: !this.state.isRoster,
      isAbout: false,
      isAccolades: false,
      isContact: false,
      isHover1: false,
      opacity: 0.3,
      isHover2: false,
      opacity2: 0.3,
      isHover3: false,
      opacity3: 0.3,
      isContactClicked: false,
      isAccoladesClicked: false,
      isAboutClicked: false
    });
  };

  isAbout_function = () => {
    this.setState({
      isAbout: !this.state.isAbout,
      isRoster: false,
      isAccolades: false,
      isContact: false,
      isAboutClicked: !this.state.isAboutClicked,
      isAccoladesClicked: false,
      isContactClicked: false,
      isHover2: false,
      opacity2: 0.3,
      isHover3: false,
      opacity3: 0.3
    });
  };

  isAccolades_function = () => {
    this.setState({
      isAccolades: !this.state.isAccolades,
      isRoster: false,
      isAbout: false,
      isContact: false,
      isAccoladesClicked: !this.state.isAccoladesClicked,
      isAboutClicked: false,
      isContactClicked: false,
      isHover1: false,
      opacity: 0.3,
      isHover3: false,
      opacity3: 0.3
    });
  };

  isContact_function = () => {
    this.setState({
      isContact: !this.state.isContact,
      isRoster: false,
      isAbout: false,
      isAccolades: false,
      isContactClicked: !this.state.isContactClicked,
      isAccoladesClicked: false,
      isAboutClicked: false,
      isHover1: false,
      opacity: 0.3,
      isHover2: false,
      opacity2: 0.3
    });
  };

  /*
   * ABOUT is first
   */
  opacity_ = () => {
    this.setState({
      opacity: 1,
      isHover1: !this.state.isHover1,
      fontSize: "large"
    });
  };
  opacity_leave = () => {
    this.setState({
      opacity: 0.3,
      isHover1: !this.state.isHover1,
      fontSize: "small"
    });

    if (this.state.isAboutClicked === true) {
      this.setState({ isHover1: true, opacity: 1 });
    } else {
      this.setState({ isHover1: false, opacity: 0.3 });
    }
  };

  /*
   * ACCOLADES is second
   */
  opacity_2 = () => {
    this.setState({
      opacity2: 1,
      isHover2: !this.state.isHover2,
      fontSize2: "large"
    });
  };
  opacity_leave2 = () => {
    this.setState({
      opacity2: 0.3,
      isHover2: !this.state.isHover2,
      fontSize2: "small"
    });

    if (this.state.isAccoladesClicked === true) {
      this.setState({ isHover2: true, opacity2: 1 });
    } else {
      this.setState({ isHover2: false, opacity2: 0.3 });
    }
  };

  /*
   * CONTACT is third
   */
  opacity_3 = () => {
    this.setState({
      opacity3: 1,
      isHover3: !this.state.isHover3,
      fontSize3: "large"
    });
  };
  opacity_leave3 = () => {
    this.setState({
      opacity3: 0.3,
      isHover3: !this.state.isHover3,
      fontSize3: "small"
    });

    if (this.state.isContactClicked === true) {
      this.setState({ isHover3: true, opacity3: 1 });
    } else {
      this.setState({ isHover3: false, opacity3: 0.3 });
    }
  };

  render() {
    const {
      isRoster,
      isHover1,
      isHover2,
      isHover3,
      isAbout,
      isAccolades,
      isContact
    } = this.state;
    return (
      <div>
        <div className="whole-page">
          <div>
            <img
              src={icon}
              width={this.state.setWidth}
              height={this.state.setHeight}
              alt="HKP icon"
            ></img>
          </div>
          <div>Hong Kong Pickles since 2019</div>
          <div>
            <button className="button" onClick={this.isRoster_function}>
              ROSTER
            </button>
          </div>
          <div className="more-nav">
            <div
              className="bottom-nav"
              onMouseEnter={this.opacity_}
              onMouseLeave={this.opacity_leave}
              style={{
                fontSize: this.state.fontSize,
                backgroundColor: `rgb(147, 148, 232, ${this.state.opacity})`
              }}
              onClick={this.isAbout_function}
            >
              <div style={{ opacity: this.state.opacity }}>About Us</div>
              {isHover1 ? <div className="bg-letter">H</div> : <div></div>}
            </div>
            <div
              className="bottom-nav"
              onMouseEnter={this.opacity_2}
              onMouseLeave={this.opacity_leave2}
              style={{
                fontSize: this.state.fontSize2,
                backgroundColor: `rgb(147, 148, 232, ${this.state.opacity2})`
              }}
              onClick={this.isAccolades_function}
            >
              <div style={{ opacity: this.state.opacity2 }}>Accolades</div>
              {isHover2 ? <div className="bg-letter">K</div> : <div></div>}
            </div>
            <div
              className="bottom-nav"
              onMouseEnter={this.opacity_3}
              onMouseLeave={this.opacity_leave3}
              style={{
                fontSize: this.state.fontSize3,
                backgroundColor: `rgb(106, 215, 115, ${this.state.opacity3})`
              }}
              onClick={this.isContact_function}
            >
              <div style={{ opacity: this.state.opacity3 }}>Contact Us</div>
              {isHover3 ? <div className="bg-letter">P</div> : <div></div>}
            </div>
          </div>
          {isRoster ? <Roster /> : <span></span>}
          {isAbout ? <About /> : <span></span>}
          {isAccolades ? <Accolades /> : <span></span>}
          {isContact ? <Contact /> : <span></span>}
        </div>
      </div>
    );
  }
}

export default App;
