import React from "react";
import Hextechcup from "../Pics/Hextechcup.png";

class Accolades extends React.Component {
  render() {
    return (
      <div style={{ backgroundColor: "rgb(120,121,188)" }}>
        <div className="h1-class">
          <h1>WINNERS</h1>
          <img src={Hextechcup} alt="winner of clash"></img>
        </div>
      </div>
    );
  }
}

export default Accolades;
